@import url(https://fonts.googleapis.com/css?family=Lato:400,700,400italic,700italic);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: lato; }

#top {
  width: 100%;
  height: 70vh;
  background: url(../../static/media/desc.cf6c2e7a.jpg) no-repeat center center;
  background-size: cover;
  position: relative; }
  #top:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #141414;
    opacity: 0.7; }

#headercontain {
  margin: auto;
  text-align: center;
  position: absolute;
  top: 36%;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  height: 30%; }

#welcome {
  font-size: 4em;
  line-height: 1em;
  text-transform: uppercase;
  font-weight: bold;
  color: #f8f8f8;
  margin-bottom: 10vh;
  padding: 0 10%; }
  @media screen and (max-width: 850px) {
    #welcome {
      font-size: 2em; } }

.arrow {
  display: block;
  text-align: center;
  margin: auto;
  width: 20px;
  outline: 0;
  height: 20px;
  background: url(data:image/svg+xml;base64,);
  background-size: contain;
  border: none !important; }

.arrow a:hover,
.arrow:active,
.arrow:focus {
  outline: 0;
  border: none !important; }

.bounce {
  -webkit-animation: bounce 0.8s infinite;
  animation: bounce 0.8s infinite; }

.bounce:hover {
  -webkit-animation: none;
  animation: none; }

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  40% {
    -webkit-transform: translateY(-30%);
    transform: translateY(-30%); }
  60% {
    -webkit-transform: translateY(-25%);
    transform: translateY(-25%); } }

@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  40% {
    -webkit-transform: translateY(-30%);
    transform: translateY(-30%); }
  60% {
    -webkit-transform: translateY(-25%);
    transform: translateY(-25%); } }

.ui.big.menu {
  margin: 0; }

footer {
  display: block;
  clear: both;
  min-height: 100px;
  width: 100%;
  margin: auto;
  background-color: #f1f1f1;
  color: #444;
  padding: 45px 0px 50px 0px;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  bottom: 0;
  text-align: center; }

.rainbow {
  height: 0.6em;
  clear: both;
  width: 100%;
  background-color: #6698cb;
  background-image: linear-gradient(to right, #82c8a0 16.66667%, #6698cb 16.66667%, #6698cb 33.33333%, #f0d264 33.33333%, #f0d264 50%, #fa5a5a 50%, #fa5a5a 66.66667%, #cb99c5 66.66667%, #cb99c5 83.33333%, #7fccde 83.33333%);
  background-size: 100% 5px; }

.section-header, .steps-header, .steps-name {
  color: #3498db;
  font-weight: 400;
  font-size: 1.4em; }

.steps-header {
  margin-bottom: 20px;
  text-align: center; }

.steps-timeline {
  outline: 1px dashed rgba(255, 0, 0, 0);
  border-left: 2px solid #3498db;
  margin: auto;
  width: 270px; }
  .steps-timeline:after {
    content: "";
    display: table;
    clear: both; }

.steps-one,
.steps-two,
.steps-three {
  outline: 1px dashed rgba(0, 128, 0, 0);
  margin-left: -25px; }

.steps-one,
.steps-two {
  padding-bottom: 40px; }

.steps-three {
  margin-bottom: -100%; }

.steps-img {
  display: block;
  margin: auto;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  float: left;
  margin-right: 20px; }

.steps-name,
.steps-description {
  margin: 0; }

.steps-description {
  overflow: hidden; }

